<template>
  <div class="home">
    <TopImage :image="topImage" />
    <div class="imagewall-section">
      <Row>
        <i-col v-for="(item,index) in wallList" span="6" :key="index">
          <div class="imagewall-item" :style="{backgroundImage: 'url('+ switchItem(item)[1] + ')'}"></div>
        </i-col>
      </Row>
    </div>
    <Carousel autoplay loop :radius-dot=true :autoplay-speed=carouselSpeed :value="carouseIndex">
      <CarouselItem v-for="(item,index) in carousel" :key="index">
        <div class="carousel-item" :style="{backgroundImage: 'url('+ item.url + ')'}"></div>
      </CarouselItem>
    </Carousel>
    <div class="ticket-section">
      <Row type="flex" justify="center" align="middle">
        <i-col span="12">
          <div class="section-title">
            
            <span></span> 
          </div>
          <div class="time">
            <p>8:30  21:00 9:00  19:30</p>
            <p>8:30  21:00 9:00  19:30</p>
          </div>
          <div class="price-info">
            <span>60/</span>
            <span>60/</span>
            <p>
              
            </p>
          </div>
          <div class="more">
            
          </div>
        </i-col>
        <i-col span="12">
          <div>
            <video src="http://www.w3school.com.cn/i/movie.ogg" controls="controls">
              your browser does not support the video tag
            </video>
          </div>
        </i-col>
      </Row>
    </div>
    <div class="news-section">
      <Row type="flex" justify="center" align="middle">
        <i-col span="24">
          <div class="section-title">
            
          </div>
          <div class="news">
            <div class="news-item">
              <h3></h3>
              <p>2018XXX2018 XXX</p>
              <span></span>
            </div>

            <div class="news-item">
              <h3></h3>
              <p>2018XXX2018 XXX</p>
              <span></span>
            </div>
            <div class="news-item">
              <h3></h3>
              <p>2018XXX2018 XXX</p>
              <span></span>
            </div>
            <div class="news-item">
              <h3></h3>
              <p>2018XXX2018 XXX</p>
              <span></span>
            </div>
            <div class="news-item">
              <h3></h3>
              <p>2018XXX2018 XXX</p>
              <span></span>
            </div>
            <div class="news-item">
              <h3></h3>
              <p>2018XXX2018 XXX</p>
              <span></span>
            </div>
          </div>
        </i-col>
      </Row>
    </div>
    <div class="imagescroll-sction">
      <swiper :options="swiperOption" class="swiper">
        <swiper-slide v-for="(item,index) in scrollList" :key="index" class="scroll-item">
          <div class="image" :style="{backgroundImage: 'url('+ switchItem(item)[1] + ')'}"></div>
          <p class="title">{{switchItem(item)[2]}}</p>
        </swiper-slide>
        <div class="swiper-pagination" slot="pagination"></div>
      </swiper>
    </div>
  </div>
</template>

<script>
import TopImage from "@/components/TopImage.vue";
import TopBanner from "../assets/banner.png";
import photoWall from "@/assets/photoWall1.png";
import config from "../config";
export default {
  data() {
    return {
      dataSrc: "http://chapi.loodp.com/js/_Carousel.js",
      wallList: [],
      scrollList: [],
      topImage: TopBanner,
      carousel: [],
      carouselSpeed: 5000,
      carouseIndex: 0,
      imgscrollValue: 0,
      imgscrollMax: 0,
      swiperOption: {
        pagination: {
          el: ".swiper-pagination",
          type: "progressbar"
        },
        slidesPerView: 3,
        spaceBetween: 30,
        autoplay: false,
        freeMode: true
      }
    };
  },
  computed: {},
  methods: {
    fetchJSData() {
      var _this = this;
      var script = document.createElement("script");
      script.type = "text/javascript";
      script.src = this.dataSrc;
      _this.$el.appendChild(script);
      script.onload = function() {
        _this.$nextTick(function() {
          _this.topImage = _Home.top.url;
          _this.carousel = _Home.carousel;
        });
      };
    },
    fetchApiData() {
      var _this = this;
      this.$http
        .get(config.ApiRoot + "/scenery/", {
          params: {
            pageCount: _this.pageSize
          }
        })
        .then(function(res) {
          if (res.data.code === 0) {
            console.log(res);
            _this.wallList = res.data.list.slice(0,4);
            _this.scrollList = res.data.list.slice(4);
          }
        })
        .catch(function(error) {});
    },
    switchItem(item) {
      return item.split("|");
    }
  },
  mounted() {
    this.fetchJSData();
    this.fetchApiData();
  },
  components: {
    TopImage
  }
};
</script>

<style lang="scss" scoped>
.imagewall-section {
  .imagewall-item {
    height: 230px;
    background-position: center;
    background-repeat: no-repeat;
  }
}
.carousel-item {
  height: 500px;
  background-position: center;
  background-repeat: no-repeat;
  background-color: #fff;
}
.ticket-section {
  height: 300px;
  padding: 50px 60px;
  background-image: url("../assets/ticketBackground.png");
  background-repeat: repeat;
  background-size: contain;
  color: #fff;
  .time {
    padding-bottom: 20px;
    p {
      text-align: left;
      font-size: 20px;
    }
  }
  .price-info {
    text-align: left;
  }
  .more {
    position: absolute;
    top: 0;
    right: 0;
    width: 15px;
    margin: 0 auto;
    line-height: 17px;
    font-size: 15px;
  }
  .ivu-row-flex {
    height: 100%;
    position: relative;
  }
}
.news-section {
  height: 400px;
  padding: 50px 60px;
  background-image: url("../assets/newsBackground.png");
  background-repeat: repeat;
  background-size: contain;
  color: #fff;
  .news {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    .news-item {
      width: calc((100% - 100px)/3);
      text-align: left;
      padding-bottom: 30px;
      h3 {
        margin-bottom: 10px;
      }
    }
  }
  .ivu-row-flex {
    height: 100%;
  }
  .ivu-col {
    height: 100%;
  }
}
.imagescroll-sction {
  height: 300px;
  background-image: url("../assets/scrollSectionBackground.png");
  background-position: center;
  .swiper {
    height: 100%;
    .swiper-pagination{
      top: auto;
      bottom: 0;
    }
  }
  .scroll-item {
    height: 80%;
    width: 33%;
    margin-right: calc(1%/3);
    flex: none;
    .image {
      height: 100%;
      background-repeat: no-repeat;
      background-position: center;
    }
    .title {
      height: 20%;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 15px;
      color: #fff;
    }
  }
}
.section-title {
  text-align: left;
  font-size: 20px;
  padding-bottom: 30px;
  span {
    font-size: 50px;
    font-weight: bold;
    position: relative;
    top: 7px;
    line-height: 20px;
  }
}
</style>



