<template>
  <div class="image" :style="{backgroundImage: 'url('+ image + ')'}"></div>
</template>

<script>
import defaultImage from "../assets/banner.png";

export default {
  name: "TopImage",
  props: {
    image: {
      type: String,
      default: defaultImage
    }
  }
};
</script>

<style scoped>
.image {
  width: 100%;
  height: 500px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
</style>




