<template>
  <div class="hotel">
    <TopImage :image="topImage" />
    <div class="content">
      <Breadcrumb>
        <span></span>
        <BreadcrumbItem></BreadcrumbItem>
        <BreadcrumbItem></BreadcrumbItem>
      </Breadcrumb>
      <div class="list">
        <ul>
          <li class="list-item" v-for="(item, index) in listData" :key="index">
            <Card>
              <div class="card">
                <img :src="item">
                <a href="#">
                  <h2></h2>
                </a>
              </div>
            </Card>
            <div class="info">
              <p>1</p>
              <p> 0977-8240254</p>
            </div>
          </li>
        </ul>
        <Page class="common-page" :total="pageTotal" :page-size="pageSize" @on-change="handlePageChange"></Page>
      </div>
    </div>
  </div>
</template>

<script>
import TopImage from "@/components/TopImage.vue";
import TopBanner from "../assets/hotelBanner.png";
import photoWall from "@/assets/photoWall1.png";
import config from "../config";
export default {
  data() {
    return {
      topImage: TopBanner,
      pageTotal: 0,
      pageSize: 16,
      listData: [photoWall, photoWall, photoWall, photoWall, photoWall]
    };
  },
  methods: {
    handlePageChange(page) {
      var _this = this;
      this.$http
        .get(config.ApiRoot + "/article/", {
          params: {
            name: "Notice",
            page: page,
            pageCount: _this.pageSize
          }
        })
        .then(function(res) {
          if (res.data.code === 0) {
            console.log(res);
            _this.pageTotal = res.data.count;
            _this.listData = res.data.list;
          }
        })
        .catch(function(error) {});
    }
  },
  components: {
    TopImage
  },
  mounted() {
    //this.handlePageChange(1);
  }
};
</script>

<style lang="scss" scoped>
.list {
  margin-bottom: 50px;
  text-align: left;
}
.list-item {
  display: inline-block;
  width: 31%;
  margin: 0 1% 30px 1%;
  text-align: center;
  .card {
    height: 260px;
    img {
      width: 100%;
      height: 90%;
      display: block;
    }
  }
  .info {
    padding: 10px 0;
  }
}
</style>





