<template>
  <div class="ticket">
    <TopImage :image="topImage" />
    <div class="content">
      <Breadcrumb>
        <span></span>
        <BreadcrumbItem></BreadcrumbItem>
        <BreadcrumbItem></BreadcrumbItem>
      </Breadcrumb>
      <div class="list">
        <ul>
          <li class="list-item" v-for="(item, index) in listData" :key="index">
            <Card>
              <div class="card">
                <img :src="item">
                <div class="card-content">
                  <div class="info">
                    <h3>title</h3>
                    <p>
                      <span>48</span></p>
                    <p>8.0 
                      <span>10</span>
                    </p>
                  </div>
                  <div class="btn">
                    <a href="#"></a>
                  </div>
                </div>
              </div>
            </Card>
          </li>
        </ul>
        <Page class="common-page" :total="pageTotal" :page-size="pageSize" @on-change="handlePageChange"></Page>
      </div>
    </div>
  </div>
</template>

<script>
import TopImage from "@/components/TopImage.vue";
import TopBanner from "../assets/ticketBanner.png";
import photoWall from "@/assets/photoWall1.png";
import config from "../config";
export default {
  data() {
    return {
      topImage: TopBanner,
      pageTotal: 0,
      pageSize: 16,
      listData: [photoWall, photoWall, photoWall, photoWall, photoWall]
    };
  },
  methods: {
    handlePageChange(page) {
      var _this = this;
      this.$http
        .get(config.ApiRoot + "/article/", {
          params: {
            name: "Notice",
            page: page,
            pageCount: _this.pageSize
          }
        })
        .then(function(res) {
          if (res.data.code === 0) {
            console.log(res);
            _this.pageTotal = res.data.count;
            _this.listData = res.data.list;
          }
        })
        .catch(function(error) {});
    }
  },
  components: {
    TopImage
  },
  mounted() {
    //this.handlePageChange(1);
  }
};
</script>

<style lang="scss" scoped>
.list {
  margin-bottom: 50px;
  text-align: left;
}
.list-item {
  display: inline-block;
  width: 24%;
  margin: 0 0.5% 30px 0.5%;
  text-align: center;
  .card {
    height: 260px;
    img {
      width: 100%;
      height: 60%;
      display: block;
    }
  }
  .card-content {
    display: flex;
    justify-content: space-between;
    height: 40%;
    margin-top: 10px;
    .info {
    }
    .btn {
    }
  }
}
</style>





