<template>
  <div class="scenery">
    <TopImage :image="topImage" />
    <div class="">
      <Breadcrumb>
        <span></span>
        <BreadcrumbItem></BreadcrumbItem>
        <BreadcrumbItem></BreadcrumbItem>
      </Breadcrumb>
      <div class="video">
        <p></p>
      </div>
      <div class="image-wall">
        <Scroll :on-reach-bottom="handleReachBottom" :height="wallHeigth">
          <div class="images-container">
            <div v-for="(item, index) in imageList" :key="index" :style="{backgroundImage: 'url('+ switchItem(item)[1] + ')'}" class="image-item"></div>
          </div>
        </Scroll>
      </div>
    </div>
  </div>
</template>

<script>
import TopImage from "@/components/TopImage.vue";
import TopBanner from "../assets/sceneryBanner.png";
import config from "../config";
export default {
  data() {
    return {
      topImage: TopBanner,
      pageSize: 20,
      imageList: [],
      wallHeigth: 600
    };
  },
  methods: {
    handleReachBottom() {
      console.log(1);
    },
    fetchImagesList() {
      var _this = this;
      this.$http
        .get(config.ApiRoot + "/scenery/", {
          params: {
            pageCount: _this.pageSize
          }
        })
        .then(function(res) {
          if (res.data.code === 0) {
            console.log(res);
            _this.imageList = res.data.list;
          }
        })
        .catch(function(error) {});
    },
    switchItem(item){
      return item.split('|');
    }
  },
  components: {
    TopImage
  },
  mounted() {
    this.fetchImagesList();
  }
};
</script>

<style lang="scss" scoped>
.video {
  background-color: #e1e1e0;
  min-height: 400px;
}
.image-wall {
  background-color: #161616;
}
.images-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  .image-item {
    height: 160px;
    width: calc(20% - 8px);
    margin: 10px 4px 0 4px;
  }
}
</style>





