<template>
  <div class="price">
    <TopImage :image="topImage" />
    <div class="content">
      <Breadcrumb>
        <span></span>
        <BreadcrumbItem></BreadcrumbItem>
        <BreadcrumbItem></BreadcrumbItem>
      </Breadcrumb>
      <div class="detail">
        <div class="common-title">
          <h1></h1>
          <h3>Opening hours and fares</h3>
        </div>
        <div class="item">
          <h1>
            <span>/OPENING</span>
            <div class="border-img"></div>
          </h1>
          <P>ASD</P>
        </div>
        <div class="item">
          <h1>
            <span>/TICKET</span>
            <div class="border-img"></div>
          </h1>
          <P>ASD</P>
        </div>
        <div class="item">
          <h1>
            <span>/DISCOUNT</span>
            <div class="border-img"></div>
          </h1>
          <P>ASD</P>
        </div>
        <div class="img">
          <img src="../assets/ticket.png" alt="">
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import TopImage from "@/components/TopImage.vue";
import TopBanner from "../assets/banner.png"
import config from "../config";
export default {
  data() {
    return {
      topImage: TopBanner,
      pageTotal: 0,
      listData: []
    };
  },
  methods: {},
  components: {
    TopImage
  },
  mounted() {}
};
</script>

<style lang="scss" scoped>
.detail {
  position: relative;
  min-height: 550px;
  .item {
    text-align: left;
    h1 {
      color: #de3a6e;
      span {
        color: #d5d5d5;
      }
    }
    .border-img {
      height: 2px;
      background: url("../assets/borderImage.png");
    }
  }
  .img {
    position: absolute;
    right: 0;
    top: 170px;
    width: 500px;
    background-color: rgb(245, 247, 249);
    img {
      width: 100%;
    }
  }
}
</style>






